*, *::before, *::after { box-sizing: border-box}

html, body {
  margin: 0;
  padding: 0;
}

.App {
  padding: 20px;;
}

nav.navbar { 
  display: flex;
  padding: 0px 20px;
  background: #f8fafa;
  position: relative;
  z-index: 1;
}

button.navbar-button {
  padding: 5px 20px;
  width: 100px;
  margin: 15px 1650px;
  border-radius: 14px;
  display:inline-block;
  border:0.1em solid #f72905;
  box-sizing: border-box;
  text-decoration:none;
  font-family:'Roboto',sans-serif;
  font-weight:300;
  color:#f37272;
  text-align:center;
  transition: all 0.2s;
}

#home-btn {
  color: rgb(10, 0, 0);
  text-decoration: none;
  padding: 0px 5px;
}

.logo{
  width: 50px;
  height: 50px;
}

.secretSelf{
  position: absolute;
  left: 850px;
}

footer.footer{
  display: flex;
  padding: 50px;
  background: #f8fafa;
  position: relative;
  z-index: 1;
  text-align: center;
}